.footer {
   padding: 20px;
   background: var(--black-t);
   &__title {
      padding-bottom: 15px;
   }
   &__body {
      display: flex;
      flex-direction: column;
      gap: 25px;
   }
   &__grid {
      display: grid;
      grid-template-rows: 1fr;
      grid-template-columns: repeat(3, 1fr);
      gap: 30px;
   }
   &__copy {
      padding: 10px 25px;
      background: var(--black-s);
      border-radius: 25px;
      text-align: center;
   }
}
.footer-item {
   padding: 25px;
   display: flex;
   flex-direction: column;
   justify-content: space-between;
   gap: 15px;
   border-radius: 25px;
   background: var(--black-s);
   &__title {
      font-size: 20px;
      padding-bottom: 5px;
   }
   &__text {
      color: var(--white-s);
      font-style: italic;
   }
   a {
      transition: 0.2s;
   }
   a:hover {
      color: #5d27ff;
   }
}
@media (max-width: $tablet) {
   .footer {
      padding: 15px;
      &__body {
         gap: 15px;
      }
      &__bottom {
         gap: 15px;
      }
      &__grid {
         grid-template-rows: repeat(3, auto);
         grid-template-columns: 1fr;
         gap: 15px;
      }
   }
   .footer-item {
      gap: 10px;
   }
}
