.header {
   width: 100%;
   position: fixed;
   top: 0;
   left: 0;
   user-select: none;
   z-index: 200;
   background: var(--black-p);
   transition: 0.3s;
   &__body {
      display: flex;
      flex-direction: column;
      gap: 30px;
      position: relative;
      border-bottom: 1px solid var(--border);
      padding: 15px 30px;
   }
   &__top {
      display: flex;
      justify-content: space-between;
   }
   &__logo {
      z-index: 5;
      display: flex;
      align-items: center;
      justify-content: center;
      min-height: 15px;
      padding-top: 4px;
   }
   &__svg {
      color: var(--white-p);
   }
   &__bottom {
      display: none;
   }
}
.menu {
   display: flex;
   align-items: center;
   gap: 20px;
   &__list {
      display: flex;
      z-index: 5;
   }
   &__item a {
      color: var(--white-p);
      padding: 10px 15px;
   }
}
.settings {
   z-index: 5;
   &__mode-btn {
      position: relative;
      min-width: 40px;
      min-height: 40px;
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 30px;
   }
   &__icon {
      color: var(--white-p);
      min-width: 27px;
      min-height: 27px;
      position: absolute;
      transition: 0.2s;
   }
   // change icon on click
   &__icon:nth-child(1) {
      opacity: 1;
   }
   &__icon:nth-child(2) {
      opacity: 0;
   }
   .darkmode &__icon:nth-child(1) {
      opacity: 0;
   }
   .darkmode &__icon:nth-child(2) {
      opacity: 1;
   }
}

@media (max-width: $mobile) {
   .header {
      height: 100%;
      background: none;
      &__body {
         display: flex;
         flex-direction: column;
         justify-content: space-between;
         height: 100svh;
         padding: 0px;
         position: relative;
      }
      &__top {
         position: relative;
         border-bottom: 1px solid var(--border);
         z-index: 5;
         padding: 15px 30px;
         background: var(--black-p);
      }
      &__bottom {
         border-top: 1px solid var(--border);
         background: var(--black-p);
         //padding: 15px 30px;
         display: flex;
         justify-content: space-around;
         text-align: center;
      }
      &__svg {
         color: var(--white-p);
      }
   }
   .menu {
      &__list {
         display: none;
      }
   }
}
