// import fonts/font <=>
@import url('https://fonts.googleapis.com/css2?family=Lexend:wght@500;700&display=swap');
// default font / default font size <=>
$fontFamily: 'Lexend', sans-serif;
$fontSize: 14;
// transition for dark mode and light
* {
   transition: color 0.2s;
   transition: background 0.2s;
}
// colors palette
:root {
   --black-p: hsl(0, 0%, 7%);
   --black-t: #141414c7;
   --black-s: hsl(0, 0%, 5%);
   --hover: #121212;
   --white-p: #ffffff;
   --white-s: #eee;
   --border: hsl(0, 0%, 14%);
   --shadow: rgba(0, 0, 0, 0.15) 0px 20px 40px -10px;
}

.darkmode {
   --black-p: hsl(0, 0%, 100%);
   --black-s: hsl(0, 3%, 93%);
   --hover-b: hsl(0, 0%, 88%);
   --black-t: #ffffffed;
   --hover: hsl(0, 0%, 88%);
   --white-p: #0d0d0d;
   --white-s: #333333;
   --border: #141414c7;
   --shadow: rgba(0, 0, 0, 0.15) 0px 20px 40px -10px;
}
// max width for container <=>
$maxWidthContainer: 1250;
// size gadjes for adapted and other <=>
$tablet: 991.98px;
$mobile: 767.98px;
$mobileSmall: 479.98px;
// null style <=>
@import 'scss/_null.scss';
// general style <=>
html {
   scroll-behavior: smooth;
}
body {
   line-height: 1.4;
   font-family: $fontFamily;
   font-size: $fontSize + px;
   background: var(--black-p);
   color: var(--white-p);
   animation: mainanim 0.7s ease-in-out;
   position: relative;
}
@keyframes mainanim {
   from {
      opacity: 0;
   }
   to {
      opacity: 1;
   }
}
// wrapper <=>
#root,
.App,
body {
   min-height: 100%;
   display: flex;
   flex-direction: column;
   .main {
      padding-top: 100px;
      flex: 1 1 auto;
   }
}
// container <=>
.container {
   max-width: $maxWidthContainer + px;
   margin: 0 auto;
   //fix for slider
   > * {
      min-width: 0;
   }
}

// scroll bar <=>
@media only screen and (max-width: $mobile) {
   ::-webkit-scrollbar {
      display: none;
   }
}
::-webkit-scrollbar {
   background-color: var(--black-p);
   width: 16px;
}
::-webkit-scrollbar-track {
   background-color: var(--black-p);
}
::-webkit-scrollbar-thumb {
   background-color: var(--white-p);
   border-radius: 16px;
   border: 4px solid var(--black-p);
}
::-webkit-scrollbar-thumb:hover {
   background-color: var(--white-s);
}
::-webkit-scrollbar-thumb:active {
   background-color: var(--white-p);
}
::-webkit-scrollbar-button {
   display: none;
}

// when copu text<=>
::selection {
   color: var(--black-p);
   background: var(--white-p);
}

// imports
@import './scss/_utils';
@import './scss/_header';
@import './scss/_footer';
